<template>
  <div>
    <becarios-asignar-periodo-modal
      :servicio="servicio"
      @actualizarTabla="actualizarTabla"
    />
    <becarios-finalizar-retirar-beca-modal
      :servicio="servicio"
      @actualizarTabla="actualizarTabla"
    />

    <b-card
      no-body
      class="mb-0 px-2 pt-1"
    >
      <div class="m-2">

        <!-- Table Top -->
        <!-- <b-row>

          <b-col
            cols="12"
            md="6"
            class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
          >
            <label>Mostrar</label>
            <v-select
              v-model="perPage"
              :options="pageOptions"
              :clearable="false"
              class="per-page-selector d-inline-block mx-50"
            />
            <label>registros</label>
          </b-col>

          <b-col
            cols="12"
            md="6"
          >
            <div class="d-flex align-items-center justify-content-end">
              <b-form-input
                v-model="filter"
                type="search"
                class="d-inline-block mr-1"
                placeholder="Buscar..."
                debounce="500"
              />
              <b-button
                v-ripple.400="'rgba(113, 102, 240, 0.15)'"
                v-b-modal.modal-add-cita
                variant="primary"
              >
                <span class="text-nowrap">Desocupar habitacn</span>
              </b-button>
            </div>
          </b-col>
        </b-row> -->

      </div>

      <!-- Main table element -->
      <b-table
        id="my-table"
        ref="refTablaUsuarios"

        class="position-relative"
        show-empty
        hover
        small
        responsive
        :busy.sync="isBusy"
        :items="myProvider"
        :fields="fields"
        :current-page="currentPage"
        :empty-text="'No hay registros para mostrar'"
        :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by="sortBy"
        :sort-desc="sortDesc"
        selectable
        caption-top
        select-mode="multi"
        selected-variant="success"
        @row-selected="onRowSelected"
      >
        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle" />
            <strong>Cargando...</strong>
          </div>
        </template>
        <!-- <template #thead-top>
          <b-tr>
            <b-th colspan="3">
              <span class="sr-only">Name and ID</span>
            </b-th>
            <b-th variant="secondary">
              Type 1
            </b-th>
            <b-th
              variant="primary"
              colspan="3"
            >
              Type 2
            </b-th>
            <b-th variant="danger">
              Type 3
            </b-th>
          </b-tr>
        </template> -->

        <template #table-caption>
          <b-row>
            <b-col
              xl="6"
              lg="6"
              md="6"
              sm="12"
              xs="12"
            >
              <span>
                <b-button
                  size="sm"
                  variant="outline-secondary"
                  @click="selectAllRows"
                >
                  Seleccionar todos
                </b-button>
                <b-button
                  size="sm"
                  variant="outline-secondary"
                  @click="clearSelected"
                >
                  Limpiar seleccin
                </b-button>
                <b-dropdown
                  variant="outline-dark"
                  class="btn-icon rounded-circle p-0"
                  size="sm"
                  right
                >
                  <template #button-content>
                    Accin masiva
                    <!-- <feather-icon
                      icon="SettingsIcon"
                      size="16"
                      class="align-middle m-0 p-0"
                    /> -->
                  </template>

                  <b-dropdown-item @click="asignarPeriodo">
                    <feather-icon icon="CalendarIcon" />
                    <span class="align-middle ml-50">Asignar periodo beca</span>
                  </b-dropdown-item>
                  <b-dropdown-item @click="finalizarBeca">
                    <feather-icon icon="CalendarIcon" />
                    <span class="align-middle ml-50">Finalizar beca</span>
                  </b-dropdown-item>
                  <b-dropdown-item>
                    <feather-icon
                      icon="MinusCircleIcon"
                      class="text-danger"
                    />
                    <span class="align-middle ml-50 text-danger">Retirar beca</span>
                  </b-dropdown-item>

                </b-dropdown>
              </span>
            </b-col>
            <b-col
              xl="6"
              lg="6"
              md="6"
              sm="12"
              xs="12"
              class="d-flex justify-content-end"
            >
              <div class="mr-1">
                <label>Mostrar</label>
                <b-form-select
                  v-model="perPage"
                  :options="pageOptions"
                  :clearable="false"
                  size="md"
                  style="width:auto"
                  class="per-page-selector d-inline-block mx-50"
                />
                <label>registros</label>
              </div>
              <div>

                <b-form-input
                  v-model="filter"
                  type="search"
                  placeholder="Buscar..."
                  size="md"
                />
              </div>
            </b-col>

          </b-row>
        </template>
        <!-- Example scoped slot for select state illustrative purposes -->
        <template #cell(selected)="{ rowSelected }">
          <template v-if="rowSelected">
            <span aria-hidden="true">
              <feather-icon
                icon="CheckIcon"
                size="30"
                class="text-danger"
              />
            </span>
            <span class="sr-only">Selected</span>
          </template>
          <template v-else>
            <span aria-hidden="true">&nbsp;</span>
            <span class="sr-only">Not selected</span>
          </template>
        </template>
        <!-- Column: User -->
        <template #cell(usuario)="data">
          <b-media vertical-align="center">
            <template #aside>
              <b-avatar
                size="32"
                :variant="`light-${resolveUserServicioVariant(data.item.persona.nombres)}`"
                :text="avatarText(data.item.persona.nombres)"
              />
            </template>
            <b-link
              class="font-weight-bold d-block text-nowrap"
            >
              {{ data.item.persona.nombre_completo }}
            </b-link>
            <small class="text-muted text-monospace py-0">DNI {{ data.item.persona.numero_documento }} - CEL: {{ data.item.persona.celular ? data.item.persona.celular : '---------' }}</small>

          </b-media>
        </template>

        <!-- Column: Status -->
        <template #cell(estado)="data">
          <b-badge
            pill
            :variant="`light-${resolvetatusVariant(data.item.estado)}`"
            class="text-capitalize"
          > {{ data.item.estado }}
            <!-- {{ data.item.estado ? 'Activo' : 'Inactivo' }} -->
          </b-badge>
        </template>

        <template #cell(habitacion)="data">
          <!-- {{ data.item.habitaciones }} -->
          <b-table-simple
            v-if="data.item.habitaciones.length > 0"
            style="font-size: 0.8em;"
            small
          >
            <b-thead>
              <b-th>Cod.</b-th>
              <b-th>Ingreso.</b-th>
              <b-th>Salida.</b-th>
            </b-thead>
            <b-tr
              v-for="(h, index) in data.item.habitaciones"
              :key="index"
            >
              <b-td><b-badge
                :variant="h.pivot.estado ? 'success' : ''"
              >
                {{ h.codigo }}
              </b-badge></b-td>
              <b-td>{{ h.pivot.fecha_ingreso }}</b-td>
              <b-td>{{ h.pivot.fecha_salida }}</b-td>
            </b-tr>
          </b-table-simple>
          <!-- <div
            v-for="(h, index) in data.item.habitaciones"
            :key="index"
          >
            <b-badge
              :variant="h.pivot.estado ? 'success' : ''"
            >
              {{ h }}
            </b-badge>

          </div> -->

        </template>
        <!-- Column: Actions -->
        <template #cell(acciones)="data">
          <b-dropdown
            variant="link"
            no-caret
            :right="$store.state.appConfig.isRTL"
          >

            <template #button-content>
              <feather-icon
                icon="MoreVerticalIcon"
                size="16"
                class="align-middle text-body"
              />
            </template>

            <!-- Asignar habitacion -->
            <b-dropdown-item @click="editarItemTriaje(data.item)">
              <feather-icon icon="HomeIcon" />
              <span class="align-middle ml-50">Asignar habitacin</span>
            </b-dropdown-item>

            <!-- Desasignar beca -->
            <b-dropdown-item
              v-b-modal.modal-2
              @click="showDesasignar(data.item)"
            >
              <feather-icon icon="UserXIcon" />
              <span class="align-middle ml-50">Desasignar habitacin</span>
            </b-dropdown-item>

            <b-dropdown-item
              @click="deleteItem(data.item)"
            >
              <feather-icon icon="TrashIcon" />
              <span class="align-middle ml-50">Quitar beca</span>
            </b-dropdown-item>
          </b-dropdown>
        </template>

      </b-table>

      <div class="mx-2 mb-2">
        <b-row>
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>

          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >

            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>

          </b-col>
        </b-row>
      </div>

    </b-card>
    <desasignar-habitacion-form-modal
      :item-habitacion="itemHabitacion"
      :item-persona="itemPersona"
      @update-table-residentes="actualizarTabla"
    />
  </div>
</template>

<script>
import {
  BCard, BTable, BSpinner, VBModal, BRow, BCol, BFormInput, BDropdown, BDropdownItem, BPagination, BBadge, BButton, BMedia,
  BAvatar,
  BFormSelect,
  BLink,
  BTr,
  BTableSimple,
  BTd,
  BTh,
  BThead,
} from 'bootstrap-vue'

// import vSelect from 'vue-select'
import { onUnmounted } from '@vue/composition-api'
import store from '@/store'
import Ripple from 'vue-ripple-directive'
import { avatarText } from '@core/utils/filter'
// import BuscarPersona from '@/views/salud/triaje/BuscarPersona.vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import DesasignarHabitacionFormModal from '../components/DesasignarHabitacionFormModal.vue'
import becarioStoreModule from './becarioStoreModule'
// import TriajeServicioModal from './TriajeServicioModal.vue'
import useSaludUtilitarios from '../useSaludUtilitarios'
import BecariosAsignarPeriodoModal from './BecariosAsignarPeriodoModal.vue'
import BecariosFinalizarRetirarBecaModal from './BecariosFinalizarRetirarBecaModal.vue'

export default {
  components: {
    BTableSimple,
    BFormSelect,
    BTable,
    BBadge,
    BTr,
    BTd,
    BThead,
    BTh,
    // vSelect,
    BSpinner,
    BCard,
    BRow,
    BCol,
    BFormInput,
    BButton,
    BDropdown,
    BDropdownItem,
    BPagination,
    BMedia,
    BAvatar,
    BLink,
    DesasignarHabitacionFormModal,
    BecariosAsignarPeriodoModal,
    BecariosFinalizarRetirarBecaModal,
  },
  directives: {
    'b-modal': VBModal,
    Ripple,
  },
  data() {
    return {
      // Props
      itemHabitacion: {},
      itemPersona: {
        persona: {
          nombre_completo: '',
          pueblo_indigena: {
            nombre: '',
          },
          sexo_nombre: '',
        },
        escuela: {
          nombre: '',
        },
      },
      servicio: [14],
      isBusy: false,
      mostrar: false,
      items: [],
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, 100],
      sortBy: 'id',
      sortDesc: false,
      sortDirection: 'desc',
      filter: '',
      filterOn: [],
      selectedPersona: {},
      selected: [],
    }
  },
  methods: {
    asignarPeriodo() {
      this.$emit('asignarPeriodo', this.selected)
    },
    finalizarBeca() {
      this.$emit('finalizarRetirarBeca', { becariosSelected: this.becariosSelected, es_retiro: true })
    },
    retirarBeca() {
      this.$emit('finalizarRetirarBeca', { becariosSelected: this.becariosSelected, es_retiro: true })
    },
    selectAllRows() {
      this.$refs.refTablaUsuarios.selectAllRows()
    },
    clearSelected() {
      this.$refs.refTablaUsuarios.clearSelected()
    },
    showDesasignar(item) {
      // console.log(item)
      this.itemPersona = item
      const { habitaciones } = item
      const [habitacion] = habitaciones.filter(i => i.pivot.estado === 1)
      this.itemHabitacion = habitacion
    },
    onRowSelected(items) {
      this.selected = items
      this.selected = this.selected.map((val => ({ becario_id: val.id, convocatoria_id: val.periodo_actual.convocatoria_id })))
    },
    editarItemTriaje(item) {
      let activos = []
      activos = item.habitaciones.filter(i => i.pivot.estado === 1)
      // console.log(activos)
      // console.log(item)
      // Si atenciones > 0 Ya no puede actualizar el triaje.
      // this.$router.push({ name: 'social-residente-asignar', params: { id: item.id } })
      if (activos <= 0) {
        this.$router.push({ name: 'residencia-becario-ambientes-list', params: { id: item.id } })
      } else {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: 'Aviso!',
            icon: 'EditIcon',
            text: 'Ya tiene habitacin',
            variant: 'warning',
          },
        })
      }
    },
    asignar(item) {
      const { usuario, ...rest } = item
      const { perfil } = usuario
      const { persona } = perfil
      this.editedItem = { ...rest, persona }

      this.$root.$emit('bv::show::modal', 'modal-aprobar-servicios', '#refModalAprobarServicios')
    },
    deleteItem(item) {
      // Se puede eliminar un triaje mientras no tenga ni un servicio aprobado.

      // Implemente swal
      this.$swal({
        title: 'Estas seguro?',
        text: 'No podrs revertir esto!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, brralo!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          this.$http.delete(`triajes/${item.id}`)
          // store.dispatch('citas/deleteCita', item)
            .then(() => {
              this.items.splice(item, 1)
              this.$swal({
                icon: 'success',
                title: 'Eliminado!',
                text: 'Tu cita ha sido eliminado.',
                customClass: {
                  confirmButton: 'btn btn-success',
                },
              })
              this.actualizarTabla()
            }).catch(error => {
              this.$swal({
                title: 'Error',
                text: `${error}`,
                icon: 'error',
                customClass: {
                  confirmButton: 'btn btn-success',
                },
              })
            })
        } else if (result.dismiss === 'cancel') {
          this.$swal({
            title: 'Cancelado',
            text: 'Tu cita est seguro :)',
            icon: 'error',
            customClass: {
              confirmButton: 'btn btn-success',
            },
          })
        }
      })
    },
    actualizarTabla() {
      this.$refs.refTablaUsuarios.refresh()
    },
    async myProvider(ctx) {
      // eslint-disable-next-line no-param-reassign
      ctx.servicios_id = [14]
      const promise = store.dispatch('residencia/fetchBecarios', { queryParams: ctx })

      // Must return a promise that resolves to an array of items
      return promise.then(response => {
        // console.log(response)
        const {
          data, total, from, to,
        } = response.data
        // Pluck the array of items off our axios response
        const items = data

        this.totalRows = total || 0
        // this.from = meta.pagination.count
        this.from = from || 0

        this.to = to || 0
        // Must return an array of items or an empty array if an error occurred
        return items || []
      })
    },
  },
  setup() {
    const RESIDENCIA_APP_STORE_MODULE_NAME = 'residencia'

    // Register module
    if (!store.hasModule(RESIDENCIA_APP_STORE_MODULE_NAME)) store.registerModule(RESIDENCIA_APP_STORE_MODULE_NAME, becarioStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(RESIDENCIA_APP_STORE_MODULE_NAME)) store.unregisterModule(RESIDENCIA_APP_STORE_MODULE_NAME)
    })

    const resolvetatusVariant = val => {
      if (val === 'activo') return 'success'
      if (val === 'inactivo') return 'danger'
      if (val === 'pendiente') return 'warning'
      return 'primary'
    }

    const { resolveColorServicioVariant } = useSaludUtilitarios()

    const fields = [
      { key: 'selected', label: '', sortable: false },
      { key: 'usuario', label: 'residente', sortable: false },
      // { key: 'convocatoria.nombre', label: 'conv.', sortable: false },
      // { key: 'modalidad.nombre', label: 'mod.', sortable: false },
      // { key: 'desdeTriaje', label: 'de triaje', sortable: false },
      // {
      //   key: 'atencion', label: 'interconsulta', sortable: false, formatter: 'serviciosInter',
      // },
      // { key: 'created_at', label: 'f. registro', sortable: false },
      { key: 'habitacion', label: 'habitacion', sortable: false },
      { key: 'periodo_actual.fecha_inicio', label: 'F.inicio', sortable: false },
      { key: 'periodo_actual.fecha_fin_programada', label: 'F.fin (programado)', sortable: false },
      { key: 'periodo_actual.fecha_fin', label: 'f.salida', sortable: false },
      {
        key: 'estado',
        sortable: true,
        // formatter: value => (value ? 'Activo' : 'Inactivo'),
      },
      { key: 'acciones' },
    ]

    const resolveUserServicioVariant = val => {
      if (val === 'FARMACIA') return 'primary'
      if (val === 'ODONTOLOGA') return 'warning'
      if (val === 'OFTAMOLOGA') return 'success'
      if (val === 'TPICO') return 'info'
      if (val === 'GINECO-OBSTTRICO') return 'danger'
      return 'primary'
    }

    return {
      // Filter
      avatarText,
      fields,
      resolvetatusVariant,
      resolveUserServicioVariant,
      resolveColorServicioVariant,
    }
  },
}
</script>
<style lang="scss" scoped>
.per-page-selector {
  width: 90px;
}
</style>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
</style>
